<template>
  <div class="hello">
      <button @click="clickquery">Options</button>
      <label>{{optionsData}}</label>
  </div>
</template>

<script>

import axios from "axios"

export default {
  name: 'Options',
  props: {
    msg: String
  },
  data(){
    return {
      optionsData:""
    }
  },
  methods:{
    clickquery(){

      var that  = this;
      var url= window.globalconfig.api_addr+"/options"
      axios.get(url,{
        headers:{
       /*  
          "Access-Control-Allow-Origin":"*",
          "Access-Control-Allow-Credentials": true,
          "Access-Control-Allow-Headers": "X-Add-Delay,X-Request-Id,X-Requested-With,Content-Type,Authorization,Authorization-Jwt,Origin,Accept",
          "Access-Control-Allow-Methods": "HEAD,OPTIONS,GET,POST,PUT,PATCH,DELETE",
          "Access-Control-Expose-Headers": "Authorization-Jwt,WWW-Authenticate,Authorization,__jwt__"
      */
        },
      }).then(function(res){
        that.optionsData = res
        console.log(res);
      }).catch(function (error) {
        console.log(error);
      });
    }
  }
}
</script>
 